"use client";

import { createSupabaseClient } from "@/lib/supaBaseClient";
import type { SupabaseClient } from "@supabase/supabase-js";
import { FileText, PieChart, Users } from "lucide-react";
import { useEffect, useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";

interface Ppt {
  name: string;
  time: string;
}

interface Subscription {
  req: number;
  total: number;
}

export default function Page({ params }: { params: { id: string } }) {
  const [ppts, setPpts] = useState<Ppt[]>([]);
  const [subscription, setSubscription] = useState<Subscription>();

  useEffect(() => {
    const client: SupabaseClient = createSupabaseClient();

    async function fetchPpts() {
      const { data, error } = await client
        .from("company")
        .select("name,time")
        .eq("name", params.id);

      if (data) setPpts(data);
    }

    async function fetchSubscription() {
      const { data, error } = await client
        .from("subs")
        .select("req,total")
        .eq("name", params.id)
        .single();

      if (data) setSubscription(data);
    }

    // fetchPpts()
    // fetchSubscription()
  }, [params.id]);

  function upgrade() {
    // Implement upgrade logic here
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 to-indigo-100 p-6 md:p-10">
      <h1 className="text-3xl md:text-4xl barlow-bold font-bold text-center text-gray-800 mb-8">
        Welcome to Your Dashboard
      </h1>

      <h1 className="barlow-bold text-2xl  text-gray-800 p-2">Stats</h1>
      <div className="montserrat grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              Total Presentations
            </CardTitle>
            <FileText className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{ppts.length}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              Subscription Usage
            </CardTitle>
            <PieChart className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">
              {subscription?.req && subscription?.total ? (
                `${((subscription.req / subscription.total) * 100).toFixed(0)}%`
              ) : (
                <h3>Unlimited</h3>
              )}
            </div>
            <Progress
              value={
                ((subscription?.req ?? 0) / (subscription?.total ?? 1)) * 100
              }
              className="mt-2"
            />
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">
              Upgrade Subscription
            </CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <Button className="w-full mt-2" onClick={upgrade}>
              Upgrade Now
            </Button>
          </CardContent>
        </Card>
      </div>

      <h1 className="mt-8 barlow-bold text-2xl  text-gray-800 p-2">
        Presentations
      </h1>
      {ppts.length == 0 ? (
        <div className="flex flex-col p-10 montserrat justify-center items-center">
          No edited ppt
          <button
            className={`px-6 py-3 ${"bg-gradient-to-r from-pink-500 to-purple-600"} rounded-lg text-white font-semibold shadow-md hover:from-pink-600 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:ring-opacity-50 transition-all duration-300`}
            onClick={handleSubmit}
          >
            Generate PPT
          </button>
        </div>
      ) : (
        <></>
      )}
      <div className=" grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {ppts.map((ppt, index) => (
          <Card key={index}>
            <CardHeader>
              <CardTitle>{ppt.name} Presentation</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Last updated: {ppt.time}
              </p>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}
